import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class TransferDataService {

  public data : any = {};

  constructor() { }

  setData(key, value){
    this.data[key] = value;
  }

  getData(key){
    if(this.data.hasOwnProperty(key))
      return this.data[key];
    return null;
  }

  clear(key = null){
    if(this.data.hasOwnProperty(key)){
      delete this.data[key];
      return;
    }

    this.data = {};
  }
}
